.input {
	position: relative;

	input {
		display: block;
		width: 100%;
		border-radius: 4px;
		border: 1px solid #fff;
		outline: none;
		background: #fff;
		height: 35px;
		padding: 6px 14px;
		font-size: 14px;
		line-height: 21px;
		font-weight: 400;
		color: #000;
		box-sizing: border-box;

		&:focus {
			border-color: #D9D9D9;
		}

		&[disabled] {
			border-color: #D9D9D9;
			background-color: #D9D9D9;
			cursor: not-allowed;
			color: #555555;
		}

		&::placeholder {
			color: #555555;
		}
	}
	&__label {
		display: block;
		position: absolute;
		left: 14px;
		top: 8px;
		font-size: 12px;
		line-height: 1;
		color: #555;

		& ~ input {
			height: 48px;
			line-height: 20px;
			padding: 20px 14px 6px 14px;
		}
	}
}
