.dropdown {
	position: relative;
	z-index: 100;

	&__select {
		display: none;
	}
	&__head {
		background: #fff;
		border-radius: 4px;
		padding: 6px 30px 6px 14px;
		cursor: pointer;
		border: 1px solid #fff;
		font-size: 14px;
		line-height: 21px;
		position: relative;
		user-select: none;

		&::after {
			content: '';
			display: block;
			position: absolute;
			right: 14px;
			top: 0;
			height: 100%;
			width: 11px;
			background: url("/images/arrow_down.svg") center no-repeat;
			background-size: 100% auto;
		}
	}
	&__body {
		display: none;
		position: absolute;
		left: 0;
		top: 100%;
		width: 100%;
		background: #fff;
		border-top: 1px solid #555;
		border-radius: 0 0 4px 4px;
	}
	&__placeholder {
		color: #555;
	}
	&__value {
		display: none;
		color: #000;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	&__option {
		display: block;
		font-size: 14px;
		line-height: 1;
		padding: 8px 14px;
		font-weight: 400;
		color: #555;
		cursor: pointer;
		user-select: none;

		&:hover,
		&.is-selected {
			color: #000;
		}
	}

	&.is-disabled {
		cursor: not-allowed;

		.dropdown__head {
			border-color: #D9D9D9;
			background-color: #D9D9D9;
			color: #555555;
			cursor: not-allowed;
		}
	}
	&.is-active {
		z-index: 300;
		.dropdown__body {
			display: block;
		}
		.dropdown__head {
			border-radius: 4px 4px 0 0;
			&::after {
				transform: scale(-1);
			}
		}
	}
	&.has-value {
		.dropdown__value {
			display: block;
		}
		.dropdown__placeholder {
			display: none;
		}
	}
	&.dropdown--2cols {
		.dropdown__options {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
		}
		.dropdown__option {
			padding: 8px 6px;
			text-align: center;
		}
	}
}
