.range {
  display: block;
  position: relative;
  font-size: 0;
  padding: 8px;

	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 50%;
		width: 100%;
		height: 2px;
		margin-top: -1px;
		background: #fff;
		border-radius: 2px;
	}

  .ui-slider {
    .ui-slider-range {
      background: currentColor;
      border-radius: inherit;
    }
    .ui-slider-handle {
      display: block;
      top: -6px;
      margin-left: -8px;
      cursor: pointer;
      outline: none;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #21221C;
		background-image: url("/images/arrow_left.svg");
		background-position: center center;
		background-repeat: no-repeat;
      border: none;

		& + .ui-slider-handle {
			background-image: url("/images/arrow_right.svg");
		}
    }
  }
  .ui-slider-horizontal {
    	height: 2px;
	  color: #21221C;
	  background: #fff;
	  border-radius: 2px;
	  border: none;
  }
}
