.form-calc {
	background: #F2F2F2;
	border-radius: 16px;
	padding: 30px 25px;

	&__head {
		font-size: 13px;
		color: #797979;
		font-weight: 400;
		margin-bottom: 20px;
	}
	&__title {
		font-size: 20px;
		line-height: 1;
		margin: 0 0 6px 0;
		font-weight: 600;
		color: #000;
	}
	&__row {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 24px;
		align-items: flex-end;
		margin-bottom: 16px;
	}
	&__range-slider {
		margin-bottom: 8px;
	}
	&__radio-group {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 8px;
	}
}
