.radio {
	input {
		display: none;
	}
	&__label {
		display: block;
		position: relative;
		cursor: pointer;
		font-size: 14px;
		line-height: 16px;
		color: #000;
		padding: 2px 2px 2px 30px;

		&::before,
		&::after {
			content: '';
			display: block;
			position: absolute;
			border-radius: 50%;
		}
		&::before {
			background: #fff;
			width: 22px;
			height: 22px;
			left: 0;
			top: 0;
		}
		&::after {
			opacity: 0;
			width: 10px;
			height: 10px;
			left: 6px;
			top: 6px;
			background: #000;
		}
	}
	input:checked + .radio__label::after {
		opacity: 1;
	}
	input:disabled + .radio__label{
		cursor: not-allowed;
		&::before {
			background-color: #D9D9D9;
		}
	}
}
